    onChange = current => {
        console.log('onChange:', current);
        this.setState({ current });
        window.scrollTo(0, 0)
    };
    ondevalueChange = e => {
        console.log('radio checked', e, e.target.value);

        this.setState({
            [e.target.name]: e.target.value,
        });
    };
    checkboxChange = (e, key) => {
        console.log('box checked', e, key);
        this.setState({
            [key]: e,
        });
    };
    nextChange = () => {
        console.log('nextChange:');
        this.setState({ current: (this.state.current + 1) })
        window.scrollTo(0, 0)
    };


    completeChange = () => {
       
        if (this.state.name === '') {
            alert('')
            this.setState({ current: 2 })
            window.scrollTo(0, 0)
        }
        else {
            const hide = message.loading('', 0);
            email(this.state)
            setTimeout(hide, 1500);
        }
    }


    debounce(fn, wait) {
        var timer = null;
        return function () {
            if (timer !== null) {
                clearTimeout(timer);
            }
            timer = setTimeout(fn, wait);
        }
    }

    onDeepChange = e => {
        console.log('deep', e)
        var key = e.target.name
        var value = e.target.value
        var dic = { [key]: value }
        if (e.target.nodeName === "INPUT") {
            this.onFormLayoutChange(dic, true)
        }
        else {
            this.onFormLayoutChange(dic)
        }

    }
    selectChange = e => {
        console.log('deep', e)
        var dic = { ['name']: e }

        this.onFormLayoutChange(dic)
    }

    timeoutflag = null;
    onFormLayoutChange = (e, tyoe = false) => {
        // console.log('onFormLayoutChange:', e, tyoe);
        for (var key in e) {
            var value = e[key]
            console.log('22', key, value)
            this.setState(
                {
                    [key]: value
                },
            )
        }
        if (tyoe === true) {
            // console.log('123')

            if (this.timeoutflag != null) {
                console.log('timeout', this.timeoutflag)
                clearTimeout(this.timeoutflag);
            }
            this.timeoutflag = setTimeout(function () {
                $.ajax({
                    type: "POST",
                    url: "http://localhost:8080/set_state",
                    data: { data: JSON.stringify(e) },
                    dataType: "JSON",
                    contentType: "application/x-www-form-urlencoded",
                });
            }, 500);
        }
        else {
            $.ajax({
                type: "POST",
                url: "http://localhost:8080/set_state",
                data: { data: JSON.stringify(e) },
                dataType: "JSON",
                contentType: "application/x-www-form-urlencoded",
            });
        }
    };

